import { addUrl, fetchUrls, deleteUrl } from "./utils/dbOperations.js";
import { getPreview } from "./utils/fetchPreview.js";

document.addEventListener("DOMContentLoaded", async () => {
    const urlForm = document.getElementById("urlForm");
    const urlList = document.getElementById("urlList");
    const thumbnailPreview = document.getElementById("thumbnail"); //  

    //  FirestoreURL
    async function loadUrls() {
        urlList.innerHTML = "";
        const urls = await fetchUrls();

        const fragment = document.createDocumentFragment(); //  DOM
        urls.forEach(({ id, data }) => {
            const li = document.createElement("li");
            li.innerHTML = `
                <img src="${data.thumbnail || 'URL'}" alt="" width="100">
                ${data.title} - <a href="${data.url}" target="_blank">${data.url}</a>
            `;

            const deleteBtn = document.createElement("button");
            deleteBtn.innerText = "";
            deleteBtn.addEventListener("click", async () => {
                await deleteUrl(id);
                loadUrls();
            });

            li.appendChild(deleteBtn);
            fragment.appendChild(li);
        });

        urlList.appendChild(fragment); //    
    }

    //  URL
    urlForm.addEventListener("submit", async (event) => {
        event.preventDefault();

        const url = document.getElementById("urlInput").value.trim();
        const title = document.getElementById("titleInput").value.trim();
        const category = document.getElementById("categoryInput").value.trim();

        if (!url || !title || !category) {
            console.error("");
            return;
        }

        //   `console.log` 
        let thumbnail = "URL";
        try {
            const imageUrl = await getPreview(url);
            console.log("URL:", imageUrl); //  
            thumbnail = imageUrl || "URL";

            //  
            thumbnailPreview.src = thumbnail;
        } catch (error) {
            console.error(":", error);
        }

        //  FirestoreID
        const userId = "user_123";  // 
        await addUrl(url, title, category, userId, thumbnail);

        urlForm.reset();
        loadUrls();
    });

    loadUrls();
});